#listBar .listBarTitle{
    display: inline-block;
    color: white;
    width:31px; height:31px;
    background:url(/static/media/add_red.48428d42.png) no-repeat;
    background-size:cover;
    font-size:80%;
    margin-right:20px;
    cursor:pointer;
    z-index:1;
}

#listBar span:not(.listBarTitle):not(.TitleDel){
    display: inline-block;
    cursor: pointer;
    color: white;
    position:relative;
    top: -7px;
    margin-left:10px;
    opacity: 0.8;
    padding:4px;
}
#listBar .TitleDel{
    display:inline-block;
    opacity:0;
    background:url(data:image/png;base64,) no-repeat;
    background-size: cover;
    width:15px;height:15px;
    margin-left:10px;
}
#listBar .TitleDel:hover{
    opacity:1;
}

#listBar span:not(.listBarTitle):hover{
    opacity: 1;
}
form{
    border-radius:6px;
    box-shadow:0px 4px 5px gray;
    background-color:white;
    width:400px; height:500px;
    padding:0; margin:0 auto 0 auto;
    position: relative;
    top:0px;
}
.mainText{
    margin: 50px auto 20px 40px;
    width:300px;height:30px;
    padding-left:10px;
}
.p_select{
    margin:20px auto auto 40px;   
}
.p_select span{
    font-size:80%;
}
.p_select select{
    margin-left:20px;
    width:240px;
}

.formButton{
    margin: 50px auto auto 303px;
}
.formButton input{
    cursor:pointer;
    width:50px;height:30px;
}
#container{
    height:0;
}
.title{
    background-color:white;
    /* border-bottom: 1px solid rgb(238, 229, 229); */
    box-shadow:0px 4px 5px gray;
    padding: 25px 0 25px 40px;
    margin:0;
    font-weight:bold;
}

.button{
    cursor:pointer;
    display:inline-block;
    position:relative;left:400px; top:-55px;
    width:40px;height:40px;
    border-radius:20px;
    background:url(/static/media/add.322159bd.png) no-repeat;
    background-size: 40px 40px;
}

.ItemCheckbox{
    cursor:pointer;
    display:inline-block;
    border: 1px lightgray solid;
    border-left:none;
    width: 40px; height: 40px;
    margin-right:5px;
    float:left;
    background: url(/static/media/ticked.50584e1d.png) no-repeat;
    background-size:cover;
}

.singleItem{
    cursor:pointer;
    border: 1px lightgray solid;
    border-left:none;border-right:none;
    height:30px;
    padding-top:10px;
    vertical-align:center;
    margin:0;
    font-size:80%;
    color:gray;
}

.singleItem .ItemText{
    width:250px;
    display: inline-block;
    overflow:hidden;
    white-space: nowrap;  
    text-overflow:ellipsis; 
}
.singleItem .tag{
    display: inline-block;
    background-color:lightgray;
    color:white;
    font-size:70%;
    float:right;
    margin-right:2px;margin-left:2px;
    padding-left:1px; padding-right:1px;padding-bottom:1px;
}




#Container{
    height:0;
}
.bookMarkContainer{
    transition-duration:0.5s;
    position:relative;
    left:360px;
    width:100px;height:26px;
    top: 150px;
}
.bookmark{
    color:white;
    font-size:70%;
    cursor:pointer;
    background-color:red;
    width:100px;
    height:22px;
    padding-left:10px;
    padding-top:4px;
  }
  
.triangle{
    cursor:pointer;
    width: 0;
    height: 0;
    border-top: 13px solid red;
    border-right: 20px solid transparent;
    border-bottom: 13px solid red;
    position: relative;
    left:110px;
    top:-26px;
}
#green{
    top:160px;
}
#green .bookmark{
    background-color:green;
}
#green .triangle{
    /* top:10px; */
    border-top: 13px solid green;
    border-bottom: 13px solid green;
}
#blue{
    top:170px;
}
#blue .bookmark{
    background-color:blue;
}
#blue .triangle{
    /* top:20px; */
    border-top: 13px solid blue;
    border-bottom: 13px solid blue;
}
