.IMGBackground{
    background: url('../../img/masterSword.jpg') center / 100% 100% no-repeat;
    position:fixed;
    left:0; top:0;
    margin:0;
    z-index:-2;
}
.IMGBackgroundScreen{
    position:fixed;
    background: black;
    opacity: 0.4;
    z-index:-1;
    width: 1800px;height: 900px;
    left:0;top:0;
    margin:0;
}
.SetBackground{
    position:fixed;
    bottom:10px;right:20px;
}
.setBackgroundButton{
    color:lightgray;
    position:relative;
    font-size: 120%;
    cursor: pointer;
}
.SetBackground #inputFiles{
    display:none;
} 

.TempBackground{
    background-color:black;
    opacity:0.7;
    position:fixed;
    left:0; top:0;
    z-index: 1;
    width:1750px;height:1000px;
    margin:0;
}

.Title_ToDoList{
    width: 250px;
    height: 30px;
    padding-left: 10px;
    margin-right: 5px;
    border: white 1px solid;
}
.AddToDoListTitle{
    height: 35px; width: 50px;
    background-color: firebrick;
    border: firebrick 1px solid;
    color: white;
}

.Title_InputDIV{
    z-index:2;
    width:330px;
    position:fixed; left:670px; top:250px;
}

